*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-size: 62.5%;
}

body{
  background-color: rgb(170, 142, 142);
}

h1{
  font-size: 3.2rem;
  margin: 2rem 0;
  text-align: center;
}

h2{
  font-size: 2.4rem;
}

p{
  margin: 2rem 0;
  color: rgba(21, 19, 60, 0.5);
  font-weight: 400;
  font-size: 1.6rem;
  display: inline-block;
}

span{
  text-transform: capitalize;
  font-weight: bold;
}

.stories-div{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 4rem;
}

.card{
  min-width: 20rem;
  width: 40vw;
  padding: 4rem;
  background-color: #ffffff;
  background-color: #cdd3dc6a;
  box-shadow: rgba(23, 23, 26, 0.8) 0px 7px 29px 0px;
  border-radius: 0.5rem;
  color: #15133c;
}

.card-button{
  flex-direction: row;
  width: 100%;
  display: flex;
  justify-content: space-between;
}


.readbtn{
  background-color: rgb(69, 64, 64);
}

.readbtn a{
  text-decoration: none;
  color: #fff;
  
}


.removebtn{
  background-color: rgb(207, 67, 67);
}

form{
  display: grid;
  place-items: center;
}

input{
  padding: 1rem;
  min-width: 40rem;
  font-size: 2rem;
  text-transform: capitalize;
 border: none;
 border-radius: 1rem;
 border-bottom: 0.2rem solid #15133c;
 outline: none;
}

.pagination-btn{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

button{
  padding: 0.8rem 2.2rem;
  border: none;
  font-size: 1.6rem;
  background-color: #15133c;
  color: #fff;
  margin: 3rem;
  border-radius: 0.5rem;
  cursor: pointer;
  display: inline-block;
}

/* responsive layout */
@media (max-width: 998px){
  html{
    font-size: 56%;
  }

  .card{
    min-width: 45rem;
    padding: 2.5rem;
  }
}

@media (max-width: 768px){
  html{
    font-size: 51%;
  }

  .card{
    min-width: 35rem;
    padding: 2rem;
  }

}


